.optin {
	margin-top: 8%;
}
.optin p {
	font-size: 14px;
	font-weight: 100;
}
.optin button {
	color: #fff;
	padding: 6px 22px;
	text-transform: uppercase;
	font-family: "Roboto Condensed", sans-serif;
	background-color: rgba(255, 255, 255, 0.1);
	border: solid 2px transparent;
	cursor: pointer;
}
.optin button:hover {
	border-color: rgba(255, 255, 255, 0.3);
}

#modal {
	position: absolute;
	top: 1%;
	left: 50%;
	transform: translate(-50%, -50%);
	margin: auto;
	width: 500px;
	background: #fff;
	transition: all 1.15s;
	box-shadow: 0 7px 2px #444;
	z-index: 99999;
	opacity: 0;
}
#modal.is_open {
	top: 50%;
	display: block;
	opacity: 100%;
}
#modal .wrapper {
	color: #444;
	text-align: center;
	padding: 30px;
}

#email-input {
	height: 30px;
	margin: 12px;
	width: 350px;
	max-width: 100%;
	border-radius: 4px;
	text-align: center;
}

input {
	height: 30px;
	margin: 12px;
	width: 350px;
	max-width: 100%;
	border-radius: 4px;
	text-align: center;
}

.span-btn {
	border: 1px solid grey;
	border-radius: 4px;
	padding: 5px;
	cursor: pointer;
}
